#wv-animation-widget {
  width: 330px;
  border-radius: 8px;
  height: 125px;
  background: #212121;
  position: absolute;
  transform: translateX(-50%);
  -webkit-transform: translate(-50%);
  top: -160px;
  left: 50%;
  display: none;
  color: #fff;
}

#wv-animation-widget.subdaily {
  width: 402px;
}

#wv-animation-widget > a,
#wv-animation-widget > div {
  display: inline-block;
  margin: 7px 0 0 10px;
}

#wv-animation-widget .wv-animation-widget-header {
  display: block;
  text-align: center;
  margin-bottom: 7px;
  text-transform: capitalize;
}

#wv-animation-widget .wv-icon-case {
  display: inline-block;
  height: 35px;
  width: 42px;
  background: #295f92;
  position: relative;
  border-radius: 5px;
}

#wv-animation-widget a .wv-animation-widget-icon {
  font-size: 28px;
  color: #fff;
  position: absolute;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
}

#wv-animation-widget a .wv-animation-widget-icon:hover {
  color: #fff;
  opacity: 0.5;
}

#wv-animation-widget a .wv-animation-widget-icon.fa-retweet {
  font-size: 34px;
  color: #212121;
  opacity: 0.7;
}

#wv-animation-widget .active .wv-animation-widget-icon.fa-retweet {
  color: #fff;
  opacity: 1;
}

#wv-animation-widget div .wv-slider-label {
  font-size: 10px;
  color: #fff;
  display: block;
  text-align: center;
}

#wv-animation-widget .wv-anim-dates-case {
  height: 48px;
  width: 100%;
  position: relative;
  margin: 0;
}

#wv-animation-widget .thru-label {
  position: absolute;
  transform: translate(-50%, 0);
  -webkit-transform: translate(-50%, 0);
  color: #fff;
  left: 50%;
  text-align: center;
  bottom: 16px;
  z-index: 0;
}

#wv-animation-widget .wv-date-selector-widget {
  display: inline-block;
  position: absolute;
  top: 6px;
}

#wv-animation-widget .wv-anim-dates-case .wv-date-selector-widget:nth-child(1) {
  left: 5px;
}

#wv-animation-widget .wv-anim-dates-case .wv-date-selector-widget:nth-child(3) {
  right: 10px;
}

#wv-animation-widget .wv-date-selector-widget svg {
  margin: 0 auto;
}

#wv-animation-widget .wv-date-selector-widget input {
  height: inherit;
  text-align: center;
  width: 100%;
}

#wv-animation-widget .wv-date-selector-widget .input-wrapper {
  z-index: 99;
  text-align: center;
  margin: 0 1px;
}

#wv-animation-widget .wv-date-selector-widget .input-wrapper-year {
  max-width: 38px;
}

#wv-animation-widget .wv-date-selector-widget .input-wrapper-month {
  max-width: 30px;
}

#wv-animation-widget .wv-date-selector-widget .input-wrapper-day,
#wv-animation-widget .wv-date-selector-widget .input-wrapper-hour,
#wv-animation-widget .wv-date-selector-widget .input-wrapper-minute,
#wv-animation-widget .wv-date-selector-widget .input-wrapper-10-minute,
#wv-animation-widget .wv-date-selector-widget .input-wrapper-5-minute,
#wv-animation-widget .wv-date-selector-widget .input-wrapper-15-minute {
  max-width: 25px;
}

#wv-animation-widget .wv-date-selector-widget .input-wrapper-hour {
  margin-left: 8px;
}

#wv-animation-widget .wv-date-selector-widget .input-time-divider {
  font-family: 'Roboto Mono', monospace;
  font-size: 15px;
  vertical-align: middle;
  width: 5px;
  margin-left: -5px;
}

#wv-animation-widget .wv-date-selector-widget .input-time-zmark {
  font-family: 'Roboto Mono', monospace;
  font-size: 15px;
  vertical-align: middle;
  margin-top: 2px;
  margin-left: -2px;
}

#wv-rangeselector-case {
  display: none;
}

.wv-anim-active #wv-animation-widget,
.wv-anim-active #wv-rangeselector-case {
  display: block;
}

.wv-animation-widget i.wv-close {
  position: absolute;
  top: 5px;
  right: 5px;
}

.wv-animation-widget i.wv-close:hover {
  color: #999;
  cursor: pointer;
}

.gif-dialog {
  color: #fff;
}

.gif-dialog .wv-button {
  margin: 10px 0 0 10px;
}

.gif-dialog .wv-button,
.gif-dialog .wv-checkbox {
  display: inline-block;
}

.gif-dialog .wv-checkbox {
  margin-left: 18px;
}

.gif-dialog select {
  margin-left: 1em;
}

.gif-results-dialog-case, {
  text-transform: capitalize;
}

.wv-gif-results .wv-button .ui-button-text,
.wv-gif-results .wv-button:hover .ui-button-text,
.wv-gif-results .ui-state-hover .ui-button-text {
  font-weight: 700;
}

.gif-selector-case {
  padding: 10px 8px 15px;
}

.gif-download-grid {
  display: flex;
  flex-wrap: wrap;
  font-size: 12px;
  font-weight: 400;
}

.gif-download-grid .grid-child {
  width: 190px;
  font-size: 12px;
  font-weight: 400;
  padding-left: 10px;
  margin-bottom: 4px;
  transition: border 0.5s ease;
}

.gif-download-grid .grid-child.label {
  width: 120px;
}

.gif-download-grid .grid-child.gif-size-invalid {
  border: 1px solid #faa;
}

.gif-results-dialog-case .gif-results-dialog,
.gif-results-dialog-case img {
  display: block;
  float: left;
}

.gif-results-dialog-case .gif-results-dialog {
  background: #fff;
  text-align: center;
  color: #444;
  vertical-align: top;
  border-radius: 6px;
  font-size: 12px;
  width: 162px;
  margin-left: 10px;
}

.gif-results-dialog-case .gif-results-dialog b {
  line-height: 30px;
  color: #000;
  font-size: 15px;
}

.wv-gif-progress {
  width: 100%;
}

.wv-checkbox-date {
  display: inline;
  position: relative;
}

.jcrop-tracker .wv-dl-gif-bt-case {
  width: inherit;
  position: relative;
  height: inherit;
}

.jcrop-tracker .wv-dl-gif-bt-case i {
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  color: #d54e21;
  position: absolute;
  transition: color 0.5s ease;
}

.jcrop-tracker .wv-dl-gif-bt-case i:hover {
  color: #f4f5f5;
  cursor: pointer;
}

.jcrop-tracker .wv-dl-gif-bt-case.disabled i,
.jcrop-tracker .wv-dl-gif-bt-case.disabled i:hover {
  cursor: default;
  color: transparent;
  pointer-events: none;
}

/* Animation GIF Styles
 * Slider styles
 * copied and modified from https://github.com/davidchin/react-input-range
 */
.input-range__slider {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: #3f51b5;
  border: 1px solid #3f51b5;
  border-radius: 100%;
  cursor: pointer;
  display: block;
  height: 15px;
  margin-left: -6.5px;
  margin-top: -8.45px;
  outline: none;
  position: absolute;
  top: 50%;
  transition: transform 0.3s ease-out, box-shadow 0.3s ease-out, -webkit-transform 0.3s ease-out, -webkit-box-shadow 0.3s ease-out;
  width: 15px;
}

.input-range__slider:active {
  -webkit-transform: scale(1.3);
  transform: scale(1.3);
}

.input-range__slider:focus {
  -webkit-box-shadow: 0 0 0 5px rgba(63, 81, 181, 0.2);
  box-shadow: 0 0 0 5px rgba(63, 81, 181, 0.2);
}

.InputRange.is-disabled .input-range__slider {
  background: #ccc;
  border: 1px solid #ccc;
  -webkit-box-shadow: none;
  box-shadow: none;
  -webkit-transform: none;
  transform: none;
}

.input-range__slider-container {
  -webkit-transition: left 0.3s ease-out;
  transition: left 0.3s ease-out;
}

.input-range__label {
  color: #fff;
  font-family: 'Open Sans', sans-serif;
  font-size: 12px;
  white-space: nowrap;
}

.input-range__label--max,
.input-range__label--min {
  display: none;
}

.input-range__label--value {
  position: absolute;
  top: -24px;
}

.input-range__label-container {
  left: -50%;
  position: relative;
}

.input-range__label--max .input-range__label-container {
  left: 50%;
}

.input-range__track {
  background: #eee;
  border-radius: 4px;
  display: block;
  height: 2.6px;
  position: relative;
  -webkit-transition: left 0.3s ease-out, width 0.3s ease-out;
  transition: left 0.3s ease-out, width 0.3s ease-out;
}

.InputRange.is-disabled .input-range__track {
  background: #eee;
}

.input-range__track--container {
  left: 0;
  margin-top: -2px;
  position: absolute;
  right: 0;
  top: 50%;
}

.wv-animation-widget .input-range {
  cursor: pointer;
  height: 13px;
  position: relative;
  width: 152px;
}

.wv-animation-widget.subdaily .input-range {
  width: 224px;
}
